import React from 'react';
import api from "../../api.js";
import './TicTacToe.css';


class PopUp extends React.Component{
    constructor(props){
        super(props);
        //Callbacks *
        this.connect = props.connect;
        this.close = props.close;
        //*
        this.state = {
            // Stages key
            // 0: No room
            // 1: Creating(Room creator only)
            // 2: Connecing(Room connector only)
            // 3: In Room
            stage: props.stage,
            roomID: props.id,
            host: false,
            pass: "",
            input: "",
        }
    }

    updateInput = (event) =>{
        this.setState({input: event.target.value})
    }

    updatePassword = (event) =>{
        this.setState({pass: event.target.value})
    }

    connectToRoom = () =>{
        this.setState({roomID: this.state.input});
        this.connect(this.state.input, this.state.pass, this.state.host);
    }

    createNewRoom = () =>{
        api.post("/api/tic/newGame", {pass: this.state.pass})
        .then(res =>{
            this.setState({roomID: res.data.id, stage: 3})
            this.connect(res.data.id, this.state.pass, this.state.host);
        })
    }

    render(){
        //Content is set based on the stage of connection
        var content = <div/>;
        console.log(this.state.roomID);
        if(this.state.stage == "0"){
            console.log("String");
        }
        console.log(this.state.input);
        switch(this.state.stage){
            case 0:
            content = <div>
                <button class = "tictac_popup_text" id = "tictac.create" onClick = {() => this.setState({host: true, stage: 1})}>
                Create New Room
                </button>
                <button class = "tictac_popup_text" id = "tictac.connect" onClick = {() => this.setState({host: false, stage: 2})}>
                    Connect To Room
                </button>
            </div>
                break;
            case 1:
                console.log("Creating Room");
                content = <div>
                    <input type = "text" placeholder = "Room Password" onChange = {this.updatePassword}/>
                    <button id = "tictac.create" onClick = {this.createNewRoom}>Create Room</button>
                </div>
                break;
            case 2:
                content = <div>
                    <input type = "text" placeholder = "Room Id" onChange = {this.updateInput}/>
                    <input type = "text" placeholder = "Room Password" onChange = {this.updatePassword}/>
                    <button id = "tictac.create" onClick = {this.connectToRoom}>Connect To Room</button>
                </div>
                break;
            case 3:
                content = <div>
                    <h3>Current Room: {this.state.roomID}</h3>
                    <button id = "tictac.leave" onClick = {() => {this.setState({stage:0})}}>
                        Leave Room
                    </button>
                </div>
                break;
            default:
                break;
        }
        return(
            <div id = "tictac-popup">
                <button id = "tictac.cancel" onClick = {() => {this.close()}}>[x]</button>
                {content}
            </div>
        );
    }
}

export default PopUp;