import React from "react";
import Headshot from "../Images/CasualClean.jpg";
import "./About.css";

function About() {
    return (
        <div className="About">
            <img id="Headshot" src={Headshot} />
            <h2 id="About-Sectiontitle">Who Am I</h2>
            <p id="About-Intro">
                I am currently studying Computer Science at the University of California Santa Cruz. As a kid, and still as an adult, I loved video
                games. Soon after its realease my father introduce me to Scratch as a way to make my own video games. I immediatley started building
                small games and fell in love with the entire process. From this point on my love for programming and computers only grew.
            </p>
            <div id="About-Lists">
                <div id="Comfort">
                    <h3 id="About-Subtitle">Comfort Zone</h3>
                    <ul class = "About-List" id="Comfort-List">
                        <li>React.js</li>
                        <li>Swift/Xcode</li>
                        <li>Python</li>
                    </ul>
                </div>
                <div id = "Goals">
                    <h3 id="About-Subtitle">Learning Goals</h3>
                <ul class = "About-List" id="Goals-List">
                    <li>SQL</li>
                    <li>Node.js</li>
                    <li>Machine Learning</li>
                </ul>
                </div>
            </div>
        </div>
    );
}

export default About;