import React from 'react';
import './Firebase.css';
import API from '../api.js';

class UserAuth extends React.Component {
    constructor(props) {
        super(props)
        this.delete = props.deletionCallback;
        this.state = {
            newAccount : false,
            goodToken: null,
            currentUser: null,
            waiting: false,
            error: false,
        }
    }

    toggleAccountCreation = () =>{
        this.setState({newAccount: !this.state.newAccount});
    }

    componentDidMount = () =>{
        this.testWrite();
    }

    login = () =>{
        this.setState({waiting: true, error: false});
        API.post('/api/auth/login', {email: this.state.email, password: this.state.password}).then(res => {
            console.log(res.data);
            localStorage.setItem('AuthIDToken',res.data.token);
            console.log(res.data.token);
            this.setState({goodToken: true, currentUser: res.data.username, waiting: false})
        })
        .catch( error => {
            console.error('Error:', error)
            this.setState({waiting: false,  error: error.message});
        })
    }

    createAccount = () =>{
        this.setState({waiting: true});
        API.post('/api/create/new-user', {email:this.state.email, password: this.state.password, displayName: this.state.username}).then(res => {
            console.log(res.data.token);
            localStorage.setItem('AuthIDToken',res.data.token);
            console.log(res.data.username);
            this.setState({goodToken: true, currentUser: res.data.username, waiting: false})
        })
    }

    testWrite = () =>{
        console.log(localStorage.getItem('AuthIDToken'));
        if(localStorage.getItem("AuthIDToken")!=null){
            API.post('/api/auth/tokenAuth', {token: localStorage.getItem("AuthIDToken")}).then(res => {
                console.log(res.data);
                this.setState({goodToken: true, currentUser: res.data.username})
            })
            .catch(error => {
                this.setState({goodToken: false})
                localStorage.removeItem('AuthIDToken');
            })
        }else{
            this.setState({goodToken: false})
        }
        console.log(this.state);
    }

    updateEmail = (event) =>{
        this.setState({email: event.target.value});
    }

    updateUsername = (event) =>{
        this.setState({username: event.target.value});
    }

    updatePass = (event) =>{
        this.setState({password: event.target.value});
    }

    render() {
        var exit = <button id = "sign-in-exit" onClick = {this.delete}>[x]</button>
        var title = <h1 id="Auth-Title">User Sign In</h1>
        if(this.state.goodToken === null){
            return(
                <div className="UserAuth">
                    {title}
                    <h2>Loading</h2>
                    <img src = 'Images/flower.gif'/>
                </div>
            )
        }
        if(this.state.goodToken){
            return(
                <div className="UserAuth">
                    {exit}
                    {title}
                    <h2>Currently Signed In As: {this.state.currentUser}</h2>
                </div>
            )
        }
        return (
            <div className="UserAuth">
                {exit}
                {title}
                {this.state.waiting ? <img id = "load-gif" src = "Images/load.gif"/>: null}
                {!this.state.error ? null: <h3>{this.state.error + ", please try again."}</h3>}
                <h3 id="Auth-Sub">Email:</h3>
                <input type="text" placeholder="cool.guy@gmail.com" onChange = {this.updateEmail}/>
                {this.state.newAccount ? <h3 id = "Auth-Sub">UserName</h3> : null}
                {this.state.newAccount ? <input type="text" placeholder="cool_guy2001" onChange = {this.updateUsername}/> : null}
                <h3 id="Auth-Sub">Password:</h3>
                <input type="text" placeholder="coolestpassword" onChange = {this.updatePass} />
                <button onClick = {this.state.newAccount ? this.createAccount : this.login}>{this.state.newAccount ? "Create Account": "Login"}</button>
                <button onClick = {this.toggleAccountCreation}>{this.state.newAccount ? "Already have an account?": "Don't have an account?"}</button>
                <button onClick = {this.testWrite}>Test</button>
            </div>
        );
    }
}

export default UserAuth;